
.lay {
  display: flex;
  width: 100%;
}

.App {
  height: 100vh;
  align-items: center;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.header {
  align-items: center;
  display: flex;
  flex-direction: row;
  margin: 20px 18px;
  max-width: 520px;
  position: relative;
  width: calc(100% - 40px);
}

.layMain {
  align-items: center;
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  width: 100%;
}

.main {
  display: flex;
  flex: 1 1 auto;
  margin: 0;
  max-width: 520px;
  width: 100%;
}

.process {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  width: 100%;
  max-width: 520px;
  justify-content: center;
  align-items: center;
}

.page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1 0 auto;
  max-width: 100%;
  width: 100%;
  margin-top: 0;
}

.section {
  background-color: #fff;
  box-shadow: 0 12px 24px rgb(45 73 88 / 15%);
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  margin: 0;
  max-width: 520px;
  min-height: auto;
  padding: 30px 20px;
  position: relative;
  width: 100%;
  z-index: 0;
}

.component {
  display: flex;
  flex: 1 0 auto;
}

.Loader {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 100%;
  width: 100%;
}


.wrap {

}

.button {
  border: 2px solid;
  border-radius: 10px;
  cursor: pointer;
  display: block;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  min-height: 50px;
  outline: none;
  overflow: hidden;
  padding: 11px 10px;
  position: relative;
  text-align: center;
  text-decoration: none;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
}